.pagination {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

a {
  color: black;
  float: left;
  padding: 8px 16px;
  text-decoration: none;
  transition: 200ms;
  text-decoration: none;
  color: black;
}
a:hover,
a:active {
  background-color: red;
  color: white;
}
